import { injectReducer } from '../../store/reducers'

export default (store) => ({
  path : 'deckbuilder',
  /*  Async getComponent is only invoked when route matches   */
  getComponent (nextState, cb) {
    /*  Webpack - use 'require.ensure' to create a split point
        and embed an async module loader (jsonp) when bundling   */
    require.ensure([], (require) => {
      /*  Webpack - use require callback to define
          dependencies for bundling   */
      const DeckBuilder = require('./containers/DeckBuilderContainer').default
      const reducer = require('./modules/deckbuilder').default

      /*  Add the reducer to the store on key 'deckbuilder'  */
      injectReducer(store, { key: 'deckbuilder', reducer })

      /*  Return getComponent   */
      cb(null, DeckBuilder)

    /* Webpack named bundle   */
  }, 'deckbuilder')
  }
})



